<template>
	<!-- 容器 -->
	<view class="scrolly_container">
		<!-- 可下拉刷新容器 -->
		<scroll-view 
		refresher-enabled
		:refresher-triggered="trigger"
		@refresherpulling="refresherpulling"
		@refresherrefresh="refresherrefresh"
		class="scrolly"
		scroll-y="true">
		<!-- 内容 -->
		<view v-for="(item,index) in dataSource" :key="item.id">
			<view
			:style="[scrollyItemStyle]"
			class="scrollyItem">
				<view class="scrollyItem_left">
					<text class="title">
						{{item.title}}
					</text>
					<view class="info_container">
						<uni-tag
						class="tag"
						size="mini"
						:text="item.tag"
						:type="item.tagType"></uni-tag>
						<text class="info" v-if="item.source">{{item.source}}</text>
						<text class="info" v-if="item.readNum">{{item.readNum}}阅读</text>
						<text class="info" v-if="item.time">{{item.time}}</text>
					</view>
				</view>
				<view v-if="item.img"
					class="scrollyItem_right">
					{{item.img}}
				</view>
			</view>
			<!-- 自定义内容的插槽 -->
			<!-- #ifdef MP -->
			<slot name="slot-{{index}}"></slot>
			<!-- #endif -->
			<!-- #ifdef H5|| APP-PLUS -->
			<slot :name="'slot-'+index"></slot>
			<!-- #endif -->
		</view>
		</scroll-view>
	</view>
</template>

<script>
	import UniTag from'../uni-tag/uni-tag.vue'
	export default {
		name:'JinScrolly',
		props:{
			dataSource:{
				type:Array,
				default:[]
			},
			itemFontSize:{
				type:Number,
				default:28
			},
			trigger:{
				type:Boolean,
				default:false
			}
		},
		methods:{
			refresherpulling(e){
				this.$emit('refresherpulling',e)
			},
			refresherrefresh(e){
				this.$emit('refresherrefresh',e)
			}
		},
		computed:{
			scrollyItemStyle(){
				let styleObj = {}
				styleObj.fontSize = this.itemFontSize + 'rpx'
				return styleObj
			}
		},
		components:{
			'uni-tag':UniTag
		}
	}
</script>

<style lang="scss" scoped>
	.scrolly_container{
		.scrolly{
			box-sizing: border-box;
			padding: 20rpx;
			.scrollyItem{
				box-sizing: border-box;
				padding: 20rpx;
				background-color: white;
				border-bottom: 1px solid #ddd;
				display: flex;
				.scrollyItem_left{
					width: 100%;
					// border: 1px solid red;
					overflow: hidden;
					
					.title{
						
					}
					.info_container{
						align-items: center;
						margin: 20rpx 0 0 0 ;
						display: flex;
						.tag {
							margin-right: 20rpx;
							border-radius: 10rpx;
						}
						.info {
							font-size: 20rpx;
							margin-right: 20rpx;
							color: #aaa;
						}
					}
				}
				.scrollyItem_right{
					// border: 1px solid red;
				}
			}
		}
	}
</style>
